/*==========  Desktop First Method  ==========*/

/* 1920 */
@media only screen and (min-width : 1999px) {
  /**/ }

@media only screen and (max-width : 1599px) {
  /**/ }

/* Large Devices, Wide Screens */
@media only screen and (max-width : 1439px) {
  .welcome {
    &__bg {
      &-right {
        img {
          transform: translateY(-250px) translateX(-330px); } } } } }

/* Large Devices, Wide Screens */
@media only screen and (max-width : 1199px) {
  .about {
    &__right {
      &-img {
        max-width: 400px; } } }
  .event {
    &__list {
      overflow-x: auto;
      &-row {
        margin: 0 -10px;
        &>div {
          padding: 0 10px; } } }
    &__block {
      width: 270px; } }
  .question {
    &__form {
      &-img {
        right: 10px;
        width: 350px; } } }
  .welcome {
    &__bg {
      &-left {
        width: 54%; }
      &-right {
        width: 46%; } } } }

/* Medium Devices, Desktops */
@media only screen and (max-width : 991px) {
  body {
    font-size: 20px; }
  .h1, h1 {
    font-size: 50px;
    margin-bottom: 30px;
    line-height: 1.25; }
  p {
    line-height: 30px;
    margin-bottom: 32px; }
  .btn {
    font-size: 20px; }
  .section {
    &.about {
      padding-top: 80px; } }
  .about {
    &__left {
      float: none;
      max-width: none; }
    &__right {
      float: none;
      &-img {
        margin: 0 0 64px 0;
        max-width: none; } } }
  .event {
    &__list {
      &-wrapper {
        overflow: inherit; }
      &-row {
        flex-wrap: nowrap;
        margin: 0;
        overflow: inherit;
        &>div {
          padding: 0;
          padding-right: 70px; } } }
    &__block {
      width: 375px;
      &-title {
        font-size: 30px;
        line-height: 45px;
        margin-bottom: 25px; }
      &-date {
        right: 15px;
        width: 76px;
        height: 80px;
        &::after {
          border-left-width: 38px;
          border-right-width: 38px;
          border-top-width: 18px;
          bottom: -18px; }
        b {
          font-size: 37px; }
        small {
          font-size: 18px;
          line-height: 25px; } } } }
  .question {
    &__form {
      height: auto;
      padding-bottom: 40px;
      &-img {
        display: none; }
      &-wrapper {
        max-width: inherit;
        .col-one {
          width: 250px; }
        .col-two {
          width: 312px;
          margin-left: 25px; } }
      &-btn {
        padding-top: 13px;
        text-align: center; } } }
  .form {
    &-group {
      margin-bottom: 26px; }
    &-control {
      height: 50px;
      font-size: 16px;
      padding-left: 25px;
      padding-right: 25px; } }
  textarea {
    &.form-control {
      height: 124px; } }
  .gallery {
    &__list {
      &-wrapper {
        flex-direction: column;
        margin: 0;
        &>div {
          width: 100%;
          padding: 0; }
        &--main {
          &>div {
            display: none;
            &:nth-of-type(-n+3) {
              display: block; } } } }
      &-btn {
        padding-top: 5px; } }
    &__block {
      border-radius: 17px;
      margin-bottom: 35px;
      &-title {
        font-size: 30px;
        left: 30px;
        bottom: 40px; } } }
  .welcome {
    height: 790px;
    &__bg {
      &-wrapper {
        width: 991px;
        &>div {
          transform: none;
          float: none;
          position: absolute;
          left: 0;
          top: 0;
          right: 0;
          bottom: 0;
          width: 100%; } }
      &-left {
        z-index: 5;
        background: rgba($color-black,.7); }
      &-right {
        img {
          transform: translateY(-250px) translateX(-190px); } } }
    &__content {
      z-index: 20;
      color: $color-white;
      &>.container {
        height: 790px; } }
    &__title {
      font-size: 30px;
      line-height: 45px;
      margin-top: 175px;
      margin-bottom: 55px;
      b {
        font-size: 50px; } }
    &__advantage {
      width: 100%;
      &>div {
        width: 33.3%;
        &+div {
          padding-left: 0; } } } } }

/* Small Devices, Tablets */
@media only screen and (max-width : 767px) {
  .section {
    padding: 30px 0;
    &.about {
      padding-top: 65px; } }
  .about {
    &__right {
      &-img {
        margin-bottom: 40px; } } }
  .event {
    &__list {
      &-row {
        &>div {
          padding-right: 58px; } } }
    &__block {
      width: 310px;
      &-title {
        font-size: 25px;
        line-height: 35px; }
      &-date {
        right: 12px;
        width: 62px;
        height: 65px;
        &::after {
          border-left-width: 32px;
          border-right-width: 32px;
          border-top-width: 14px;
          bottom: -14px; }
        b {
          font-size: 30px; }
        small {
          font-size: 15px;
          line-height: 20px; } } } }
  .question {
    &__form {
      &-wrapper {
        .col-one, .col-two {
          float: none;
          width: 100%; }
        .col-two {
          margin-left: 0; } } } }
  .gallery {
    &__list {
      &-btn {
        padding-top: 18px; } }
    &__block {
      border-radius: 10px;
      margin-bottom: 22px;
      &-title {
        left: 20px;
        bottom: 20px;
        font-size: 20px; } } }
  .welcome {
    height: auto;
    &__bg {
      position: unset;
      &-right {
        img {
          transform: translateX(-190px);
          height: 100%;
          object-fit: cover; } } }
    &__advantage {
      flex-direction: column;
      justify-content: center;
      &>div {
        width: 100%;
        &+div {
          padding-top: 28px; } } }
    &__content {
      position: inherit;
      padding-bottom: 120px;
      &>.container {
        height: auto; } }
    &__title {
      margin: 100px 0 50px 20px; } } }

/* Extra Small Devices, Phones */
@media only screen and (max-width : 479px) {
  body {
    font-size: 15px; }
  .h1, h1 {
    font-size: 30px;
    margin-bottom: 35px; }
  p {
    line-height: 20px;
    margin-bottom: 15px; }
  .btn {
    font-size: 15px; }
  .section {
    &.about {
      padding-top: 40px; } }
  .about {
    &__right {
      padding-top: 10px; } }
  .event {
    &__list {
      &-row {
        &>div {
          padding-right: 40px; } } }
    &__block {
      width: 208px;
      &-title {
        font-size: 18px;
        line-height: 25px;
        margin-bottom: 13px; }
      &-date {
        right: 7px;
        width: 42px;
        height: 44px;
        &::after {
          border-left-width: 21px;
          border-right-width: 21px;
          border-top-width: 10px;
          bottom: -10px; }
        b {
          font-size: 20px; }
        small {
          font-size: 10px;
          line-height: 14px; } } } }
  .question {
    &__form {
      padding-left: 20px;
      padding-right: 20px;
      &-wrapper {} } }
  .gallery {
    &__list {
      &-btn {
        padding-top: 25px; } }
    &__block {
      margin-bottom: 15px;
      &-title {
        left: 14px;
        bottom: 14px;
        font-size: 15px; } } }
  .welcome {
    &__title {
      font-size: 20px;
      margin: 60px 0 45px 10px;
      line-height: 30px;
      b {
        font-size: 35px; } }
    &__content {
      padding-bottom: 65px; } } }


