/* modules */

.row {
  @include clearfix;
  &--flex {
    display: flex; }
  &>[class*='col-'] {
    float: left;
    &6 {
      width: 50%; } } }

.content {
  &__head {
    .h1, h1 {
      font-family: $font-axiform-b;
      position: relative;
      padding-bottom: 22px;
      &::before {
        position: absolute;
        width: 233px;
        height: 3px;
        left: 0;
        bottom: 0;
        background: $primary-color;
        content: ''; } } } }

.section {
  padding: 45px 0;
  &.about {
    padding-top: 90px; } }

.about {
  &__left {
    float: left;
    max-width: 430px; }
  &__right {
    float: right;
    text-align: center;
    &-img {
      overflow: hidden;
      margin-top: -15px;
      max-width: 530px;
      img {
        display: block;
        max-width: 100%;
        height: auto; } } } }

.event {
  &__list {
    overflow: hidden;
    &-wrapper {
      overflow: hidden; }
    &-row {
      display: flex;
      align-items: flex-end;
      flex-wrap: wrap;
      margin: 0 -34px;
      &>div {
        padding: 0 33px; } } }
  &__block {
    width: 300px;
    margin-bottom: 40px;
    position: relative;
    &>a {
      position: absolute;
      z-index: 25;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0; }
    &-img {
      position: relative;
      overflow: hidden;
      border-radius: 10px;
      .span {
        opacity: 0;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 19px;
        font-family: $font-axiform-b;
        z-index: 5;
        transition: opacity 0.35s; }
      img {
        max-width: 100%;
        height: auto;
        display: block;
        opacity: 1;
        transition: opacity 0.35s; } }
    &-date {
      position: absolute;
      top: 0;
      right: 10px;
      background: $color-white;
      height: 63px;
      display: block;
      width: 60px;
      text-align: center;
      font-size: 15px;
      font-family: $font-axiform-r;
      padding-top: 3px;
      opacity: 1;
      z-index: 5;
      transition: color 0.35s;
      small {
        display: block;
        line-height: 20px;
        font-size: 15px;
        margin-top: -3px;
        position: relative;
        z-index: 5; }
      b {
        display: block;
        line-height: 1;
        font-size: 30px;
        font-weight: 400;
        font-family: $font-axiform-r; }
      &::after {
        content: '';
        position: absolute;
        left: 0;
        bottom: -15px;
        width: 0;
        height: 0;
        border-left: 30px solid transparent;
        border-right: 30px solid transparent;
        border-top: 15px solid $color-white; } }
    &-title {
      display: inline-block;
      text-decoration: none;
      color: $primary-color;
      font-size: 20px;
      font-family: $font-axiform-r;
      vertical-align: bottom;
      line-height: 30px;
      margin-bottom: 15px;
      opacity: 1;
      transition: opacity 0.35s;
      &:hover {
        color: $primary-color; } }

    &:hover {
      .event__block-title, .event__block-img img {
        opacity: 0.3; }
      .event__block-date {
        color: #c0c0c0; }
      .event__block-img .span {
        opacity: 1; } } } }

.question {
  &__form {
    position: relative;
    background: #efefef;
    border-radius: 10px;
    padding: 24px 30px;
    height: 396px;
    @include clearfix;
    &-img {
      position: absolute;
      right: 43px;
      bottom: 0; }
    &-wrapper {
      max-width: 470px;
      .col-one {
        width: 200px; }
      .col-two {
        width: 250px;
        margin-left: 20px; } }
    &-btn {
      padding-top: 20px; } } }

.gallery {
  &__list {
    &-wrapper {
      display: flex;
      flex-wrap: wrap;
      margin: 0 -15px;
      &>div {
        padding: 0 10px;
        width: 33.3%; } }
    &-btn {
      text-align: center;
      padding-top: 20px; } }
  &__block {
    position: relative;
    margin-bottom: 20px;
    border-radius: 7px;
    overflow: hidden;
    font-size: 15px;
    font-family: $font-axiform-r;
    img {
      max-width: 100%;
      height: auto;
      display: block;
      opacity: 1;
      transition: opacity 0.35s; }
    &-title {
      position: absolute;
      color: $color-white;
      z-index: 5;
      left: 20px;
      bottom: 20px; }
    .span {
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      color: $primary-color;
      opacity: 1;
      visibility: hidden;
      display: flex;
      justify-content: center;
      align-items: center;
      z-index: 15;
      font-size: 20px;
      font-family: $font-axiform-b; }
    a {
      position: absolute;
      z-index: 50;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0; }
    &:hover {
      img {
        opacity: 0.3; }
      .span {
        opacity: 1;
        visibility: visible; } } } }

.welcome {
  height: 633px;
  background-color: $color-gray;
  padding: 0 !important;
  position: relative;
  overflow: hidden;
  &__bg {
    position: relative;
    height: 100%;
    overflow: hidden;
    &-wrapper {
      position: absolute;
      width: 2300px;
      left: 50%;
      top: 0;
      bottom: 0;
      transform: translateX(-50%);
      &>div {
        float: left;
        height: 100%;
        transform: skewX(-10deg);
        overflow: hidden; } }
    &-left {
      background: $color-white;
      width: 51%; }
    &-right {
      width: 49%;
      background: {
        color: $color-gray; }
      img {
        position: absolute;
        top: 0;
        bottom: 0;
        transform: translateY(-155px) translateX(-158px); } } }
  &__content {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    &>.container {
      height: 633px;
 } }      // background: rgba(pink,.5)
  &__title {
    font-size: 30px;
    line-height: 45px;
    margin-top: 67px;
    margin-bottom: 38px;
    b {
      font-weight: 400;
      font-size: 50px;
      font-family: $font-axiform-r;
      display: block;
      line-height: 1; } }
  &__advantage {
    display: flex;
    &>div {
      &+div {
        padding-left: 50px; } }
    &-block {
      text-align: center;
      font-size: 20px;
      width: 120px;
      margin: 0 auto;
      p {
        margin: 0 0 0 -5px;
        line-height: 30px;
        white-space: nowrap; } }
    &-icon {
      height: 120px;
      width: 120px;
      position: relative;
      background: $color-white;
      border-radius: 50%;
      overflow: hidden;
      box-shadow: 0 0 50px rgba($color-blue,.2);
      margin: 0 auto 20px;
      .icon {
        position: absolute;
        left: 13px;
        top: 13px; } } } }
